/* CSS */
.product-container {
    border: 1px solid #ccc;
    padding: 10px;
    border-radius: 5px;
    width: 200px;
    margin: 10px;
    display: inline-block;
    text-align: center;
  }
  
  .product-thumbnail {
    width: 100px;
    height: 100px;
    object-fit: cover;
    border-radius: 5px;
    margin-bottom: 10px;
  }
  
  .product-title {
    font-size: 16px;
    font-weight: bold;
    margin-bottom: 5px;
  }
  
  .product-price {
    font-size: 14px;
    margin-bottom: 5px;
  }
  
  .product-discount {
    font-size: 14px;
    margin-bottom: 5px;
    color: green;
  }
  
  .product-description {
    font-size: 14px;
  }
  